@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Inter", sans-serif;
}

html {
  font-size: 16px;
}

img#profile_img {
  height: 6rem;
  width: 6rem;
  border-radius: 100%;
}

#email,
input[type="text"] {
  height: 2rem;
  width: 15rem;
  margin-top: 5px;
}

body {
  background-color: #ffffff;
  text-align: center;
  min-height: 100vh;
  max-width: 100vw;
}
.contact_container {
  background-color: #ffffff;
  text-align: center;
  min-height: 100vh;
  max-width: 100vw;
}


#message{
  height: 5rem;
  width: 15rem;
}

header,
main,
footer {
  max-width: 85vw;
  margin: 0 auto;
}

header {
  margin: 2rem 0;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

header a {
  display: block;
  color: #101828;
  font-size: 1.2rem;
  font-weight: bold;
  text-decoration: none;
  margin-top: 1rem;
}

.profile {
  margin-left: 15rem;
  position: relative;
}

main .buttons a {
  display: block;
  text-decoration: none;
  color: #101828;
  background-color: #eaecf0;
  padding: 1.5rem;
  margin: 0 auto;
  margin-top: 1rem;
  width: 90%;
  border-radius: 0.5rem;
  font-weight: bold;
  transition: all 0.3s ease-in-out;
}

main .buttons a:hover {
  background-color: #d0d5dd;
}

.hidden,
.option_icon {
  display: none;
}

.share_icon {
  margin-left: 3rem;
}

.icons {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  margin: 1.5rem 0;
}

small {
  color: #667085;
}

.logo1 {
  margin-left: 50px;
}

footer {
  border-top: 1px solid #eaecf0;
  margin: 3rem 0 0;
  padding: 1rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 100px;
}

.footer2 {
  display: none;
}

@media (max-width: 767px) {
  .profile {
    margin-left: 4rem;
  }

  .hover {
    margin-left: -1rem;
  }

  main .buttons a {
    width: 95%;
  }

  main .buttons a:hover {
    width: 95%;
    background-color: #dde1e7;
    box-shadow: -2px -2px 3px #ffffff, 2px 2px 3px rgba(201, 202, 204, 0.945);
  }

  .footer2 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .footer2 img {
    padding: 7px 0 7px 0;
    width: 150px;
  }
}
